/* Row container */

.row {
    margin-left: 20px;
    color: white;
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

/* Normal Row Styles */

.row_poster {
    object-fit: contain;
    width: 100%;
    max-height: 100px;
    transition: transform .35s;
    margin-right: 5px;
}

.row_poster:hover {
    transform: scale(1.08);
    opacity: 1;
}

/* Large Row Styles */

.row_posterLarge {
    max-height: 250px;
}

.row_posterLarge:hover {
    transform: scale(1.09);
    opacity: 1;
}

/* Broken Row */

.broken_row {
    display: none;
}
